<template>
    <ul class="footer" :style="{background:bg}">
        <li><router-link to="/move/moveList">电影</router-link> </li>
        <li><router-link to="/music">音乐</router-link> </li>
        <li><router-link to="/book">书籍</router-link> </li>
        <li><router-link to="/photo">图片</router-link> </li>
    </ul>
</template>

<script>
    export default {
        name: "Footer",
        props:['bg'],
    }
</script>

<style scoped>
    .footer{
        height: 1rem;
        background-color: rgb(33,150,243);
        position: fixed;
        bottom: 0;
        width: 100%;
        line-height: 1rem;
        /*将父元素分块  需要先设置父元素*/
        display: flex;
    }
    li{
        flex-grow: 1;
        text-align: center;
        color: #ffff;
    }
    .footer li a{
        color: #cccc;
    }
</style>


