<template>
<div>
     <!-- <ul class="move-list">
        <li @click="geDetail(move.id)" v-for="move in moveList" :key="move.id" class="move">
  <router-link to="/move/moveDetail">
             <div class="move-img"><img :src="move.img" alt=""> </div>
            <div class="move-info">
                <p class="move-name">{{move.nm}}</p>
                <p>{{move.ver}}</p>
                <p>主演:{{move.star}}</p>
                <p>{{move.showInfo}}</p>
              </div>
              </router-link>
        </li>
        </ul>  -->


    <ul class="move-list">
        <li @click="geDetail(move.id)" v-for="move in moveList" :key="move.id" class="move">
            <div class="move-img"><img :src="move.img" alt=""> </div>
            <div class="move-info">
                <p class="move-name">{{move.nm}}</p>
                <p>{{move.ver}}</p>
                <p>主演:{{move.star}}</p>
                <p>{{move.showInfo}}</p>
              </div>
        </li></ul>
         <div class="tip" v-show="tip">
            数据到底了！
        </div>
    <div class="loading" v-show="loadingShow"><img src="../../assets/img/loading.gif" alt=""></div>
</div>
</template>
<script>
import  Axios from 'axios'
import { fail } from 'assert';
    export default {
        name: "MoveList",
        data(){
            return{
                moveList:[],
                loadingShow:true,
                tip:false,
            }
        },
        mounted(){
            this.loadData();
                 window.onscroll=()=>{
                let clientHeight=document.documentElement.clientHeight;
                let scrollTop=document.documentElement.scrollTop;
                let scrollheigth=document.documentElement.scrollHeight;
                scrollTop=Math.ceil(scrollTop);
                if(clientHeight+scrollTop===scrollheigth){
                    console.log("到底了");
                    this.loadingShow=true;
                    if(!this.tip){
                       this.loadData();     
                    }else{
                        this.loadingShow=false;
                    }
                
                }
                // console.log(clientHeight+' '+scrollTop+' '+scrollheigth)
           }
 
        },
        methods:{
            loadData(){
         let url2 = '/static/data/moviedata.json'
            Axios.get(url2).then(res=>{
                // console.log(res);
                let list=res.data.movieList;
                //截取十个一组显示
                var data=list.slice(this.moveList.length,this.moveList.length+10);
                if(data.length<10){
                    this.tip=true;
                }
                this.moveList=this.moveList.concat(data);
                this.loadingShow=false;
            }).catch(()=>{})
            },
             geDetail(moveId){
                console.log(moveId);
                this.$router.push('/move/moveDetail/'+moveId);
                
            }   
       }
    }
</script>

<style scoped>
.move-list{
    padding: 1rem 0;
}
.move{
display: flex;
padding:0.2rem;
border:1px solid #ccc;
}
.move-img{
    flex-grow: 1;
    width: 0;
    margin-right: .2rem;
}
.move-info{
  flex-grow: 2;
  width: 0;
}
.move-name{
    font-weight: bolder;
}
.loading{
    text-align: center;
    position: relative;
    z-index: 10;
    bottom: 50px;
}
.tip{
      text-align: center;
    position: relative;
    z-index: 10;
    bottom: 50px;
    color: red;
}
</style>


