<template>
   <div class="detail">
       <div class="detail-content">
          <div class="detail-img">
            <img :src="detail.img" alt="">
        </div>
        <div class="detail-info">
            <h3 v-text="detail.nm"></h3>
            <p>评分:{{detail.sc}}</p>
            <p>{{detail.cat}}</p>
            <p>{{detail.src}}/{{detail.dur}}分钟</p>
            <p>{{detail.rt}}</p>
        </div>
       </div>
       <hr>
       <div>
           <h3>主演</h3>
           <p>{{detail.star}}</p>
       </div>
       <hr>
       <div>
           <h3>简介</h3>
           <p>{{detail.dra}}</p>
       </div>
   </div>
</template>

<script>
import  Axios from 'axios'
    export default {
        name: "MoveDetail",
        data(){
            return{
                detail:{},
            }

        },
        mounted(){
            Axios.get('/static/data/moviedetail.json').then(res=>{
                this.detail=res.data.detailMovie;
                console.log(this.detail);

            }).catch(err=>{
                console.log('err');
            })
        }
    }
</script>

<style scoped>
.detail{
    margin: 1rem 0;
    padding: 0 0.2rem;
    text-align: center;
}
.detail-content{
    display: flex
}
.detail-img{
    flex: 1;
    width: 0;
}
.detail-info{
    flex: 2;
}
</style>


