<template>
   <div class="header" :style="{background:bg}">
       <button class="home" @click="goHome()">首页</button>
       <h2 class="title">
       <slot name="title">加载中...</slot>
       </h2>
   </div>
</template>

<script>
    export default {
        name: "Header",
        methods:{
            goHome(){
                this.$router.push("/");
            }
        },
        props:['bg']//改变属性的时候配置
    }
</script>

<style scoped>
    .header{
        height: 1rem;
        background-color: rgb(33,150,243);
        position: fixed;
        top: 0;
        width: 100%;
        line-height: 1rem;
    }
    .home{
        color: #ffff;
    }
    .title{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
    }

</style>


