import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Move from '@/components/move/Move'
import MoveList from '@/components/move/MoveList'
import MoveDetail from '@/components/move/MoveDetail'
import Music from '@/components/music/Music'
import MusicList from '@/components/music/MusicList'
import MusicAlbums from '@/components/music/MusicAlbums'
import Book from '@/components/book/Book'
import BookList from '@/components/book/BookList'
import BookDetail from '@/components/book/BookDetail'
import Photo from '@/components/photo/Photo'
import PhotoDetail from '@/components/photo/PhotoDetail'
import PhotoList from '@/components/photo/PhotoList'
Vue.use(Router)
export default new Router({
  routes: [
    {
        path: '/',
        name: 'Login',
        component: Login,
        //重定向
        redirect:'/move/moveList'
    },
      { 
          path:'/move',
          component:Move,
          redirect:'/move/moveList',

          //子组件
          children:[
              {
                  path:'moveList',
                  component:MoveList
              },
              {
                  path:'moveDetail/:moveId',
                  component:MoveDetail
              }

          ]
      },
      {
          path:'/music',
          component:Music,
          redirect:'/music/musicList',
          children:[
              {
                  path:'musicList',
                  component:MusicList

              },
              {
                  path:'musicAlbums/:musicId',
                  component:MusicAlbums

              }
          ]
      },
      {
          path:'/book',
          component:Book,
          redirect:'/book/bookList',
          children:[
              {
                path:'bookList',
                component:BookList,
            },
            {
                path:'bookDetail',
                component:BookDetail
            }
             

          ]

      },
      {
          path:'/photo',
          component:Photo,
          redirect:'/photo/photoList',
          children:[
              {
                  path:'photoList',
                  component:PhotoList
              },
              {
                  path:'photoDetail/:index',
                  component:PhotoDetail
              }
          ]

      }

  ]
})



